.skills {
  width: 100%;

  text-align: center;

  color: rgb(36, 24, 24);
  background: #1e656d;

  padding: 50px 0;
}

.skills__wrapper {
  width: 80%;
  max-width: 1440px;

  margin: 0 auto;
}

.skills__title {
  color: orange;

  margin: 0 0 50px;
}

.skills__text {
  color: orange;

  margin: 0 0 30px;
}

.skills__list {
  .reset-list;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;

  color: white;

  counter-reset: counter-name;
}

.skills__item {
  width: 50%;

  text-align: center;

  position: relative;

  padding-top: 50px;
  margin-bottom: 25px;

  &::before {
    counter-increment: counter-name;
    content: counter(counter-name);

    width: 40px;
    height: 40px;


    position: absolute;
    top: 1px;
    left: 50%;
    transform: translateX(-50%);

    border: 4px solid deepskyblue;
    border-radius: 50%;

    color: black;
    background: white;

    padding-top: 2px;
    box-sizing: border-box;
  }
}
